import React, { useState, useEffect, useRef, useCallback } from 'react';
import Scrollbar from 'smooth-scrollbar';
import '../../styles/Career/Career.css';

const Career = React.forwardRef((props, ref) => {
  const wrapperRef = useRef(null);

  const [careerData, setCareerData] = useState([]);
  const [isVisible, setIsVisible] = useState(false);

  // ---------- FETCH DATA ----------
  const fetchCareerData = useCallback(async () => {
    try {
      const res = await fetch('/cfg/careerData.cfg');
      if (!res.ok) throw new Error('careerData.cfg 불러오기 실패');
      const data = await res.json();
      setCareerData(data);
    } catch (err) {
      console.error('Career data fetch error:', err);
    }
  }, []);

  useEffect(() => {
    fetchCareerData();
  }, [fetchCareerData]);

  // ---------- Smooth Scrollbar ----------
  useEffect(() => {
    if (!wrapperRef.current) return;
    const sb = Scrollbar.init(wrapperRef.current, { damping: 0.08, alwaysShowTracks: true });
    return () => sb.destroy();
  }, []);

  // ---------- TIMELINE ANIMATION ----------
  useEffect(() => {
    if (!wrapperRef.current) return;
    const observer = new IntersectionObserver(
      entries => entries.forEach(entry => setIsVisible(entry.isIntersecting)),
      { threshold: 0.3 }
    );
    observer.observe(wrapperRef.current);
    return () => observer.disconnect();
  }, []);

  return (
    <div className="page-content-3" ref={ref}>
      <h2 className="title">CAREER</h2>
      <div className="timeline-wrapper" ref={wrapperRef}>
        <div className={`timeline ${isVisible ? 'show' : ''}`}>
          {careerData.map((step, index) => (
            <div className="timeline-step" key={step.id || index}>
              <div className={`dot ${index === 0 ? 'blink' : ''}`}></div>
              <div className="content">
                <div className="content-container">
                  <p>{step.date}</p>
                  <h3>{step.title}</h3>
                </div>
                <p>{step.description}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
});

export default Career;