import React, { useState, useEffect, useRef } from 'react';
import { useNavigate, useLocation } from "react-router-dom";
import Scrollbar from 'smooth-scrollbar';
import '../../styles/About/about.css';

const About = React.forwardRef(({ scrollbar }, ref) => {
    const introRef = useRef(null);
    const scrollerRef = useRef(null);
    const location = useLocation();
    const navigate = useNavigate();

    const [isHovered, setIsHovered] = useState(false);
    const [companyName, setCompanyName] = useState("");
    const [introData, setIntroData] = useState([]);
    const [skills, setSkills] = useState([]);
    const [ip, setIp] = useState("");
    const [isVisible, setIsVisible] = useState(false);

    // ---------- FETCH DATA ----------
    useEffect(() => {
        Promise.all([
            fetch('./cfg/ip.cfg').then(res => res.json()).then(data => setIp(data.pc)),
            fetch('./cfg/skillsData.cfg').then(res => res.json()).then(data => setSkills(data)),
            fetch('./cfg/introData.cfg').then(res => res.json()).then(data => setIntroData(data))
        ]).catch(err => console.error('데이터 로딩 실패:', err));
    }, []);

    // ---------- 회사명 가져오기 ----------
    useEffect(() => {
        if (!ip) return;
        fetch(`${ip}/moti`, {
            headers: {
                "ngrok-skip-browser-warning": "any-value"
            }
        })
        .then(res => res.json())
        .then(data => data?.name && setCompanyName(data.name))
        .catch(err => console.error("회사명 가져오기 실패:", err));
    }, [ip]);

    // ---------- location state 업데이트 ----------
    useEffect(() => {
        if (location.state?.updatedCompany) setCompanyName(location.state.updatedCompany);
    }, [location.state]);

    // ---------- Smooth Scrollbar ----------
    useEffect(() => {
        if (!introRef.current) return;
        const sb = Scrollbar.init(introRef.current, { damping: 0.08, alwaysShowTracks: true });
        return () => sb.destroy();
    }, []);

    // ---------- SKILL ANIMATION ----------
    useEffect(() => {
        if (!scrollerRef.current || skills.length === 0) return;

        const inner = scrollerRef.current.querySelector(".scroller__inner");
        inner.innerHTML = '';

        skills.forEach(skill => {
            const li = document.createElement('li');
            li.textContent = skill.name;
            const icon = document.createElement('i');
            icon.className = `bx ${skill.icon}`;
            icon.style.color = skill.color;
            icon.style.marginLeft = "5px";
            li.appendChild(icon);
            inner.appendChild(li);
        });

        const children = Array.from(inner.children);
        for (let i = 0; i < 5; i++) {
            children.forEach(child => inner.appendChild(child.cloneNode(true)));
        }

        const singleSetWidth = inner.scrollWidth / 3;
        inner.style.setProperty('--scroll-width', `-${singleSetWidth}px`);

        const scrollSpeed = 50;
        const duration = singleSetWidth / scrollSpeed;
        inner.style.animation = `scroll ${duration}s linear infinite`;

    }, [skills]);

    // ---------- INTRO ANIMATION ----------
    useEffect(() => {
        if (!introRef.current) return;
        const observer = new IntersectionObserver(
            entries => entries.forEach(entry => setIsVisible(entry.isIntersecting)),
            { threshold: 0.3 }
        );
        observer.observe(introRef.current);
        return () => observer.disconnect();
    }, []);

    // ---------- RENDER HELPER ----------
    const renderAnswer = (answerArr) => {
        if (!answerArr) return null;
        const htmlString = answerArr.join('<br/>').replace(/__COMPANY__/g, companyName);
        return <span dangerouslySetInnerHTML={{ __html: htmlString }} />;
    };

    return (
        <div className="page-content" ref={ref}>
            {/* EMOJI */}
            <div className='emoji-container'>
                <img
                    src={isHovered ? '/img/emoji2.png' : '/img/emoji1.png'}
                    alt='err_emoji1_pic'
                    className="emoji1"
                    style={{ cursor: 'none' }}
                    onMouseEnter={() => setIsHovered(true)}
                    onMouseLeave={() => setIsHovered(false)}
                    onClick={(e) => {
                        e.stopPropagation();
                        navigate(`/about/me`, { state: { background: location } });
                    }}
                />
            </div>

            {/* SKILL */}
            <main>
                <link
                    href="https://unpkg.com/boxicons@2.1.4/css/boxicons.min.css"
                    rel="stylesheet"
                />
                <div className="scroller" ref={scrollerRef}>
                    <ul className="tag-list scroller__inner">
                        {skills.map((skill, i) => (
                            <li key={i}>
                                {skill.name}
                                <i
                                    className={`bx ${skill.icon}`}
                                    style={{ color: skill.color, marginLeft: "5px" }}
                                ></i>
                            </li>
                        ))}
                    </ul>
                </div>
            </main>

            {/* INTRODUCE */}
            <div className="introduce-wrapper" ref={introRef}>
                <div className={`introduce-container ${isVisible ? "show" : ""}`}>
                    {introData.map((item) => (
                        <div key={item.id} className={item.id}>
                            <div className="intro-first-top">
                                <h3>{item.question}</h3>
                                {item.hasEdit && localStorage.getItem("token") && (
                                    <button
                                        className="edit-btn"
                                        onClick={(e) => {
                                            e.stopPropagation();
                                            navigate(`/about/moti`, { state: { background: location } });
                                        }}
                                    >
                                        ✏️
                                    </button>
                                )}
                            </div>
                            <p>{renderAnswer(item.answer)}</p>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
});

export default About;