import React from "react";
import "../../styles/Home/RisingCircle.css";

export default function RisingCircle({ count = 5 }) {
    return (
        <>
            {Array.from({ length: count }).map((_, index) => (
                <div className="rising-circle" key={index}></div>
            ))}
            <img src="/img/employeeID.png" alt="rising" className="rising-image" style={{ left: '50%' }} />
        </>
    )
}