import React, { useState, useEffect, useRef } from 'react';
import Scrollbar, { ScrollbarPlugin } from 'smooth-scrollbar';
import Header from '../../components/Layout/header';
import Layout from '../../components/Layout/Layout';
import About from '../About/About';
import Spec from '../Project/Project';
import Career from "../Career/Career";
import Ai from "../Ai/Ai";
import Down from "../Down/Down";
import RisingCircle from './RisingCircle';
import CustomCursor from './CustomCursor';
import '../../styles/Home/home.css';

// Smooth ScrollPlugin
class DisableScrollPlugin extends ScrollbarPlugin {
    static pluginName = 'disableScroll';
    transformDelta(delta) {
        return this.options.disabled ? { x: 0, y: 0 } : delta;
    }
}
Scrollbar.use(DisableScrollPlugin);

const Home = () => {
    const [cursorActive, setCursorActive] = useState(false);
    const [currentIndex, setCurrentIndex] = useState(0);

    const scrollRef = useRef(null);
    const scrollbarRef = useRef(null);

    const texts = ["열정 있는", "책임감이 강한", "웹을 사랑하는"];

    // ----------------- Cursor 처리 -----------------
    useEffect(() => {
        document.body.style.cursor = cursorActive ? 'none' : 'auto';
        return () => { document.body.style.cursor = 'auto'; };
    }, [cursorActive]);

    // ----------------- Smooth Scrollbar 초기화 -----------------
    useEffect(() => {
        if (!scrollRef.current) return;
        const scrollbar = Scrollbar.init(scrollRef.current, { damping: 0.1 });
        scrollbarRef.current = scrollbar;

        const handleScroll = ({ offset }) => {
            const scrollY = offset.y;
            const sectionHeight = window.innerHeight;

            const newCursorActive = 
            Math.floor(scrollY / sectionHeight) === 0 || Math.floor(scrollY / sectionHeight) === 3 ||  Math.floor(scrollY / sectionHeight) === 4;
            setCursorActive(prev => prev !== newCursorActive ? newCursorActive : prev);

            const newIndex = Math.min(texts.length - 1, Math.floor(scrollY / (sectionHeight * 0.08)));
            setCurrentIndex(prev => prev !== newIndex ? newIndex : prev);
        };
        scrollbar.addListener(handleScroll);

        return () => scrollbar.destroy();
    }, [texts.length]);

    return (
        <Layout>
            <CustomCursor active={cursorActive} />
            <div className="scroll-container" ref={scrollRef}>
                {/* 첫 번째 페이지 */}
                <div className="page-section page-1" id="section01">
                    <Header />
                    <RisingCircle />
                    <p data-cursor="clickable" className={`intro-title`} style={{ cursor: "none" }}>
                        <span className={`intro-01`}>{texts[currentIndex]}</span>
                        <br />
                        <span className={`intro-02`}>개발자</span>{' '}
                        <span className={`intro-03`}>최광혁 입니다.</span>
                    </p>
                </div>

                {/* 페이지's */}
                <div className="page-section page-2"><About scrollbar={scrollbarRef.current} /></div>
                <div className="page-section page-3"><Spec /></div>
                <div className="page-section page-4"><Career /></div>
                <div className="page-section page-5"><Ai /></div>
                <div className="page-section page-6" style={{ height: "100vh" }}>
                    <p className={`intro-title`}>
                        <span className={`intro-04`}>이력서</span>{' '}
                        <span className={`intro-05`}>다운로드</span>
                    </p>
                    <div className="down-container">
                        <Down />
                    </div>
                </div>
            </div>
        </Layout>
    );
};

export default Home;
