import React, { useEffect, useState } from 'react';
import { useNavigate } from "react-router-dom";
import '../../styles/Project/ProjectInsModal.css';

const ProjectInsModal = ({ onClose, onSave }) => {
    const navigate = useNavigate();

    const [title, setTitle] = useState("");
    const [desc, setDesc] = useState("");
    const [manager, setManager] = useState("");
    const [skill, setSkill] = useState("");
    const [date, setDate] = useState("");
    const [member, setMember] = useState([{ key: "", value: "" }]);
    const [mainFunc, setMainFunc] = useState([{ key: 1, value: "" }]);
    const [workPerformed, setWorkPerformed] = useState([{ key: "", value: "" }]);
    const [images, setImages] = useState([]);
    const [ip, setIp] = useState("");

    useEffect(() => {
        fetch('/cfg/ip.cfg')
            .then(res => res.json())
            .then(data => setIp(data.pc))
            .catch(err => console.error('ip.cfg 불러오기 실패:', err));
    }, []);

    // ---------- FUNCTION SETTING ----------
    const handleImageUpload = (e) => {
        const file = e.target.files[0];
        if (!file) return;

        const previewUrl = URL.createObjectURL(file);
        setImages((prev) => [
            ...prev,
            { key: Date.now(), file, preview: previewUrl }
        ]);
    };

    const handleRemoveImage = (key) => {
        setImages((prev) => prev.filter((img) => img.key !== key));
    };

    const handleRemoveMember = (index) => {
        if (index !== 0) {
            setMember((prev) => prev.filter((_, i) => i !== index));
        }
    };

    const handleRemoveMainFunc = (index) => {
        if (index !== 0) {
            setMainFunc((prev) => prev.filter((_, i) => i !== index));
        }
    };

    const handleRemoveWorkPerformed = (index) => {
        if (index !== 0) {
            setWorkPerformed((prev) => prev.filter((_, i) => i !== index));
        }
    };

    // ---------- API REQUEST ----------
    const handleSubmit = async () => {
        const formData = new FormData();
        const data = {
            title,
            desc,
            manager,
            skill,
            date,
            member,
            mainFunc,
            workPerformed,
        };

        formData.append("data", JSON.stringify(data));
        images.forEach((img, index) => {
            formData.append("files", img.file);
        });

        try {
            const response = await fetch(`${ip}/project`, {
                method: "POST",
                body: formData,
            });

            const result = await response.json();
            alert("프로젝트 생성 성공!");
            console.log("프로젝트 생성 데이터:", result);
            navigate("/", { state: { refresh: true } });
        } catch (error) {
            console.error("업로드 실패:", error);
            alert("서버와 연결할 수 없습니다.");
        }
    };

    const closeDialog = () => navigate("/", { state: { refresh: true } });

    return (
        <div className="p-insert-dialog-overlay">
            <button className="dialog-close" onClick={closeDialog}>x</button>
            <div className="p-insert-dialog-box" onClick={(e) => e.stopPropagation()}>
                <h2>프로젝트 추가</h2>
                <hr className="divider" />
                <div className="form-group">
                    <label>제목</label>
                    <input value={title} onChange={(e) => setTitle(e.target.value)} />
                </div>

                <div className="form-group">
                    <label>설명</label>
                    <textarea value={desc} onChange={(e) => setDesc(e.target.value)} />
                </div>

                <div className="form-group">
                    <label>담당 업무</label>
                    <input value={manager} onChange={(e) => setManager(e.target.value)} />
                </div>

                <div className="form-group">
                    <label>사용 기술</label>
                    <input value={skill} onChange={(e) => setSkill(e.target.value)} />
                </div>

                <div className="form-group">
                    <label>기간</label>
                    <input value={date} onChange={(e) => setDate(e.target.value)} />
                </div>

                <div className="form-group">
                    <label>이미지</label>
                    <input type="file" accept="image/*" onChange={handleImageUpload} />
                    <div className="preview-images">
                        {images.map((img) => (
                            <div key={img.key} className="preview-wrapper">
                                <img key={img.key} src={img.preview} alt="preview" className="preview-img" />
                                <button
                                    className="ins-remove-image-btn"
                                    onClick={() => handleRemoveImage(img.key)}
                                >
                                    ❌
                                </button>
                            </div>
                        ))}
                    </div>
                </div>

                <div className="form-group">
                    <label>인원</label>
                    {member.map((m, i) => (
                        <div key={i} className="dynamic-row">
                            <input
                                placeholder="역할"
                                value={m.key}
                                onChange={(e) => {
                                    const updated = [...member];
                                    updated[i].key = e.target.value;
                                    setMember(updated);
                                }}
                            />
                            <input
                                placeholder="인원수"
                                value={m.value}
                                onChange={(e) => {
                                    const updated = [...member];
                                    updated[i].value = e.target.value;
                                    setMember(updated);
                                }}
                            />
                            <button
                                className="remove-member-btn"
                                onClick={() => handleRemoveMember(i)}
                            >
                                ❌
                            </button>
                        </div>
                    ))}
                    <button className='add-btn' onClick={() => setMember([...member, { key: "", value: "" }])}>+ 추가</button>
                </div>

                <div className="form-group">
                    <label>주요 기능</label>
                    {mainFunc.map((f, i) => (
                        <div key={i} className="dynamic-row">
                            <input
                                placeholder="기능 입력"
                                value={f.value}
                                onChange={(e) => {
                                    const updated = [...mainFunc];
                                    updated[i].value = e.target.value;
                                    setMainFunc(updated);
                                }}
                            />
                            <button
                                className="remove-func-btn"
                                onClick={() => handleRemoveMainFunc(i)}
                            >
                                ❌
                            </button>
                        </div>
                    ))}
                    <button className='add-btn' onClick={() => setMainFunc([...mainFunc, { key: Date.now(), value: "" }])}>+ 추가</button>
                </div>

                <div className="form-group">
                    <label>수행 업무</label>
                    {workPerformed.map((w, i) => (
                        <div key={i} className="dynamic-column">
                            <input
                                placeholder="업무 제목"
                                value={w.key}
                                onChange={(e) => {
                                    const updated = [...workPerformed];
                                    updated[i].key = e.target.value;
                                    setWorkPerformed(updated);
                                }}
                            />
                            <textarea
                                placeholder="업무 설명"
                                value={w.value}
                                onChange={(e) => {
                                    const updated = [...workPerformed];
                                    updated[i].value = e.target.value;
                                    setWorkPerformed(updated);
                                }}
                            />
                            <div className="remove-btn-wrapper">
                                <button
                                    className="remove-work-btn"
                                    onClick={() => handleRemoveWorkPerformed(i)}
                                >
                                    ❌
                                </button>
                            </div>
                        </div>
                    ))}
                    <button className='add-btn' onClick={() => setWorkPerformed([...workPerformed, { key: "", value: "" }])}>+ 추가</button>
                </div>

                <div className="modal-actions">
                    <button className="save-btn" onClick={handleSubmit}>저장</button>
                </div>
            </div>
        </div>
    );
};

export default ProjectInsModal;