import React, { useEffect, useState, useRef, useCallback, memo } from 'react';
import { useNavigate, useLocation } from "react-router-dom";
import Scrollbar from 'smooth-scrollbar';
import '../../styles/Project/Project.css';

const ProjectCard = memo(({ card, index, ip, isActive, onToggle, onDetail }) => (
  <div
    className={`card ${isActive ? 'active' : ''}`}
    onClick={() => onToggle(index)}
  >
    <div className="side front">
      <div className="img">
        {Array.isArray(card.images) && card.images.length > 0 ? (
          <img
            src={`${ip}${card.images[0].filePath}`}
            alt={card.title}
            className="card-image"
            loading="lazy"
          />
        ) : (
          <span className="no-image-text">회사 저작권 이미지</span>
        )}
      </div>

      <div className="info">
        <h2 className="card-title">{card.title}</h2>
        <p>{card.desc}</p>
      </div>
    </div>

    <div className="side back">
      <div className="info">
        <h2>{card.title}</h2>
        <div className='card-backdesc'>
          <p>{card.desc}</p>
          <hr className="divider" />
          <div>
            <span className="label">담당 업무: </span>
            <span className="value">{card.manager}</span>
          </div>
          <div className="desc-row">
            <span className="label">사용 기술: </span>
            <span className="value">{card.skill}</span>
          </div>
          <div className="desc-row">
            <span className="label">기간: </span>
            <span className="value">{card.date}</span>
          </div>
        </div>
        <div className='info-detail-btn-container'>
          <button
            className='info-detail-btn'
            onClick={(e) => {
              e.stopPropagation();
              onDetail(card);
            }}
          >
            자세히 보기
          </button>
        </div>
      </div>
    </div>
  </div>
));

const Spec = React.forwardRef((props, ref) => {
  const introRef = useRef(null);
  const scrollbarInstance = useRef(null);

  const navigate = useNavigate();
  const location = useLocation();

  const [ip, setIp] = useState("");
  const [cardData, setCardData] = useState([]);
  const [activeCards, setActiveCards] = useState([]);
  const [activeFilter, setActiveFilter] = useState('전체');

  const toggleCard = useCallback((index) => {
    setActiveCards(prev =>
      prev.map((active, i) => (i === index ? !active : active))
    );
  }, []);

  // ---------- FETCH DATA ----------
  useEffect(() => {
    fetch('/cfg/ip.cfg')
      .then(res => res.json())
      .then(data => setIp(data.pc))
      .catch(err => console.error('ip.cfg 불러오기 실패:', err));
  }, []);

  // ---------- INIT SMOOTHSCROLLBAR ----------
  useEffect(() => {
    if (introRef.current && !scrollbarInstance.current) {
      scrollbarInstance.current = Scrollbar.init(introRef.current, {
        damping: 0.08,
        alwaysShowTracks: true,
      });
    }
    return () => {
      scrollbarInstance.current?.destroy();
      scrollbarInstance.current = null;
    };
  }, []);

  // ---------- API REQUEST ----------
  const fetchProjects = useCallback(async () => {
    try {
      const response = await fetch(`${ip}/project`, {
        headers: { 
          "Content-Type": "application/json",
          "ngrok-skip-browser-warning": "any-value" 
        },
      });
      if (!response.ok) throw new Error("서버 응답 실패");

      const data = await response.json();
      console.log(data);
      setCardData(data);
      setActiveCards(Array(data.length).fill(false));
    } catch (error) {
      console.error("프로젝트 조회 실패:", error);
      alert("서버와 연결할 수 없습니다.");
    }
  }, [ip]);

  useEffect(() => {
    if (!ip) return;
    fetchProjects();
    if (location.state?.refresh) {
      navigate(location.pathname, { replace: true });
    }
  }, [ip, location.state, location.pathname, navigate, fetchProjects]);

  const handleDetail = useCallback(
    (card) => {
      navigate(`/spec/project/${encodeURIComponent(card.title)}`, {
        state: { project: card, background: location },
      });
    },
    [navigate, location]
  );

  // ---------- DATA FILTER ----------
  const filterCards = React.useMemo(() => {
    const filtermenu = ["제목 순", "기간 순"];
    switch(activeFilter){
      case filtermenu[0]:
        return [...cardData].sort((a, b) => a.title.localeCompare(b.title));
      case filtermenu[1]:
        return [...cardData].sort((a, b) => {
          const getStartDate = (str) => new Date(str.split("~")[0].trim().replace(/\./g, "-"));
          return getStartDate(b.date) - getStartDate(a.date);
        });
      default :
        return cardData;
    }
  }, [cardData, activeFilter])


  return (
    <div className="page-content-2" ref={ref}>
      <h2 className='title'>PROJECT</h2>
      <div className='projct-btn-container'>
        {['전체', '기간 순', '제목 순'].map((label) => (
          <button
            key={label}
            className={activeFilter === label ? 'active' : ''}
            onClick={() => setActiveFilter(label)}
          >
            {label}
          </button>
        ))}
      </div>
      <div className="project-wrapper" ref={introRef}>
        <div className='cardContainer'>
          {filterCards.map((card, index) => (
            <ProjectCard
              key={card.id || index}
              card={card}
              index={index}
              ip={ip}
              isActive={activeCards[index]}
              onToggle={toggleCard}
              onDetail={handleDetail}
            />
          ))}
        </div>
      </div>
      {localStorage.getItem("token") && (
        <button
          className="add-card-btn"
          onClick={(e) => {
            e.stopPropagation();
            navigate(`/spec/project/add`, { state: { background: location } });
          }}
        >
          +
        </button>
      )}
    </div>
  );
});

export default Spec;