import React, { useEffect, useState } from "react";
import "../../styles/Home/CustomCursor.css";

const CustomCursor = ({ active }) => {
    const [position, setPosition] = useState({ x: 0, y: 0 });
    const [hoveringClickable, setHoveringClickable] = useState(false);

    useEffect(() => {
        if (!active) return;

        const moveCursor = (e) => setPosition({ x: e.clientX, y: e.clientY });
        const handleMouseOver = (e) => {
            const target = e.target;
            const isClickable =
                target.dataset.cursor === "clickable" ||
                target.classList?.contains("navbar-toggler-icon") ||
                target.classList?.contains("intro-01") ||
                target.classList?.contains("intro-02") ||
                target.classList?.contains("intro-03") ||
                target.classList?.contains("intro-04") ||
                target.classList?.contains("intro-05") ||
                target.classList?.contains("emoji1") ||
                target.classList?.contains("phone-number") ||
                target.classList?.contains("dl-parachute") ||
                target.classList?.contains("arrow") ||
                target.classList?.contains("ai-svg") ||
                target.id?.includes("main") ||
                target.tagName === "A" ||
                target.tagName === "BUTTON" ||
                target.tagName === "INPUT" ||
                target.getAttribute("role") === "button" ||
                window.getComputedStyle(target).cursor === "pointer";

            if (isClickable) setHoveringClickable(true);
        };

        const handleMouseOut = () => setHoveringClickable(false);

        window.addEventListener("mousemove", moveCursor);
        window.addEventListener("mouseover", handleMouseOver);
        window.addEventListener("mouseout", handleMouseOut);

        return () => {
            window.removeEventListener("mousemove", moveCursor);
            window.removeEventListener("mouseover", handleMouseOver);
            window.removeEventListener("mouseout", handleMouseOut);
        };
    }, [active]);

    if (!active) return null;

    return (
        <div
            className={`custom-cursor ${hoveringClickable ? "cursor-hover" : ""}`}
            style={{ left: position.x, top: position.y }}
        />
    );
};

export default CustomCursor;
