import React, { useState, useEffect, useCallback, useRef } from "react";
import "../../styles/Ai/AiPage.css";
import Scrollbar from "smooth-scrollbar";
import Ai from "./AiCircle";

const AiPage = React.forwardRef((props, ref) => {
  const [ip, setIp] = useState("");
  const [aiResult, setAiResult] = useState("");
  const [careerData, setCareerData] = useState([]);
  const [skillData, setSkillData] = useState([]);
  const [introData, setIntroData] = useState([]);
  const [isExpanded, setIsExpanded] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  const scrollAreaRef = useRef(null);
  const scrollbarRef = useRef(null);

  // ---------- Smooth Scrollbar 초기화 ----------
  useEffect(() => {
    if (!scrollAreaRef.current) return;
    scrollbarRef.current = Scrollbar.init(scrollAreaRef.current, {
      damping: 0.1,
      alwaysShowTracks: true,
    });
    return () => scrollbarRef.current?.destroy();
  }, []);

  useEffect(() => {
    scrollbarRef.current?.update();
  }, [aiResult]);

  // ---------- FETCH DATA ----------
  useEffect(() => {
    const fetchAllData = async () => {
      try {
        const [ipRes, skillsRes, introRes, careerRes] = await Promise.all([
          fetch("/cfg/ip.cfg").then((res) => res.json()),
          fetch("./cfg/skillsData.cfg").then((res) => res.json()),
          fetch("./cfg/introData.cfg").then((res) => res.json()),
          fetch("./cfg/careerData.cfg").then((res) => res.json()),
        ]);

        setIp(ipRes.pc || "");
        setSkillData(skillsRes || []);
        setIntroData(introRes || []);
        setCareerData(careerRes || []);
      } catch (error) {
        console.error("초기 데이터 불러오기 실패:", error);
      }
    };

    fetchAllData();
  }, []);

  // ---------- FETCH PROJECT DATA ----------
  const fetchProjects = useCallback(async () => {
    try {
      const res = await fetch(`${ip}/project`, {
        headers: {
          "Content-Type": "application/json",
          "ngrok-skip-browser-warning": "any-value"
        }
      });
      if (!res.ok) throw new Error("서버 응답 실패");
      return await res.json();
    } catch (err) {
      console.error("프로젝트 불러오기 실패:", err);
      alert("서버와 연결할 수 없습니다.");
      return [];
    }
  }, [ip]);

  // ---------- API REQUEST ----------
  const fetchAiSummary = useCallback(
    async (payload) => {
      try {
        setIsLoading(true);
        const res = await fetch(`${ip}/gpt`, {
          method: "POST",
          headers: { 
            "Content-Type": "application/json",
            "ngrok-skip-browser-warning": "any-value" 
          },
          body: JSON.stringify({ data: payload }),
        });

        if (!res.ok) throw new Error("GPT API 요청 실패");

        const result = await res.json();
        setAiResult(result.summary || "AI 요약을 가져오는 데 실패했습니다.");
      } catch (error) {
        console.error("GPT 요청 실패:", error);
        setAiResult("AI 요약을 가져오는 데 실패했습니다.");
      } finally {
        setIsLoading(false);
        scrollbarRef.current?.update();
      }
    },
    [ip]
  );

  // ---------- CLICK EVENT ----------
  const handleClick = useCallback(async () => {
    if (!ip) return;

    const projects = await fetchProjects();
    const payload = { careerData, skillData, introData, projects };
    fetchAiSummary(payload);
  }, [ip, careerData, skillData, introData, fetchProjects, fetchAiSummary]);

  return (
    <div className="page-content-4" ref={ref}>
      <h1 className="ai-title">AI SUMMARY</h1>
      <p className="ai-subtitle">
        클릭 시 AI가 지원동기, 성향, 목표, 프로젝트, 스펙을 요약해서 보여줍니다.
      </p>

      <div className={`ai-card`} onClick={handleClick}>
        {isLoading ? (
          <div className="spinner-container">
            <div className="spinner"></div>
          </div>
        ) : (
          <Ai />
        )}
      </div>

      <div className="ai-textarea-wrapper">
        <div
          className={`ai-scroll-area ${isExpanded ? "expanded" : ""}`}
          ref={scrollAreaRef}
        >
          <div className="ai-scroll-content">{aiResult}</div>
        </div>

        <button
          className="expand-btn"
          onClick={() => setIsExpanded(prev => !prev)}
          style={{ cursor: "none" }}
        >
          {isExpanded ? "-" : "+"}
        </button>
      </div>
    </div>
  );
});

export default AiPage;
